<script>
import { random_string } from "../../libraries/random_string";

export let id = "switch-"+random_string(6);
export let name = "";
export let checked = `checked='checked'`;
export let on_change = function(){};
export let classes = "";
</script>
<style>
.field-switch-component{
    display: flex;
    align-items: center;
    gap: 10px;
}
.field-switch-component :global(label){
    display: flex;
    align-items: center;
}
.field-switch-component :global(.switch[type="checkbox"]+label::before), .field-switch-component :global(.switch[type="checkbox"]+label:before){
    height: 0.75rem;
    width: 2rem;
    background: var(--color-dark-grey)
}
.field-switch-component :global(.switch[type="checkbox"]:checked+label::before), .field-switch-component :global(.switch[type="checkbox"]:checked+label:before){
    background: var(--color-dark-grey)
}

.field-switch-component :global(.switch[type="checkbox"]+label){
    padding-left: 2.25rem;
}
.field-switch-component :global(.switch[type="checkbox"]:not(:checked)+label::after), .field-switch-component :global(.switch[type="checkbox"]:not(:checked)+label:after){
    left: 0;
    background: var(--color-dark-grey)
}
.field-switch-component :global(.switch[type="checkbox"]:checked+label::after), .field-switch-component :global(.switch[type="checkbox"]:checked+label:after){
    background: linear-gradient(45deg,  #2A6FFF 0%, #15B8FF 100%);
    left: 1.125rem;
}
</style>
<div class="field field-switch-component {classes}">
    <slot name="label1" id={id}></slot>
        <input on:change="{(e)=>{ on_change(e); }}" id="{id}" type="checkbox" name="{name}" class="switch is-rounded" {checked}>
    <slot name="label2" id={id}></slot>
</div>
