<script>
export let classes = "";
export let classes_content = "";
export let classes_container = "";
export let style_checkbox_content = "width:100%;";
export let style_checkbox_container = "";
export let has_circle = false;
export let is_column = false;

</script>
<checkbox-radio role="checkbox" class="input-radio {classes}" aria-checked="false">
    <!-- svelte-ignore a11y-label-has-associated-control -->
    <label style="margin-bottom: 0px !important;">
        <slot></slot>
        <checkradio-container class="{classes_container} {is_column ? "is-flex-direction-column-reverse": ""}" style="{style_checkbox_container}">
            {#if has_circle}
                <checkbox-circle class="input-circle flex-center">
                    <div class="inner-circle"></div>
                </checkbox-circle>
            {/if}
            <checkbox-content class="{classes_content}" style="{style_checkbox_content}">
                <slot name="content"></slot>
            </checkbox-content>
        </checkradio-container>
    </label>
</checkbox-radio>